<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">关联回款</span>
				</div>
			</div>
			<div class="relete_back_title flex_between">
				<div class="">
					本次已选择待分配总额<span>{{ total_selected_money }}万元</span>
				</div>
				<!--<div class="blue_btn cursor">
					确定
				</div>-->
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table ref="multipleTable" :data="relate_table_data" @selection-change="handleSelectionChange"  border style="width: 100%" >
						<el-table-column type="selection" width="55">
    						</el-table-column>
    						<el-table-column prop="event" label="序号" width="100" sortable>
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column prop="receive_date" label="到账日期" width="180" sortable>
						</el-table-column>
						<el-table-column prop="event" label="事件名称" width="180" sortable>
						</el-table-column>
						<el-table-column prop="fund_name" label="标的名称" width="180" sortable>
						</el-table-column>
						<el-table-column prop="payback_type_name" label="回款类型" width="180" sortable>
						</el-table-column>
						<el-table-column prop="unit" label="币种" width="120" sortable>
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column prop="sum_back" label="回款返本（万元）" width="180" sortable>
						</el-table-column>
						<el-table-column prop="sum_profile" label="回款收益-税后（万元）" width="220" sortable>
						</el-table-column>
						<el-table-column prop="sum_calculate" label="回款总额（万元）" width="180" sortable>
						</el-table-column>
						<el-table-column prop="rest_fee" label="待分配余额（万元）" width="180" sortable>
						</el-table-column>
						<!--<el-table-column fixed="right" label="操作" width="120">
							<template slot-scope="scope">
								<el-button type="text" size="small" @click="toClick(scope.row)">查看</el-button>
							</template>
						</el-table-column>-->
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		<!--分配模块开始-->
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">分配</span>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table ref="" :data="total_table_data" border style="width: 100%">
						<el-table-column prop="plan_receive_date" label="计划到账日期" width="200" sortable>
							<template slot-scope="scope">
								<el-date-picker v-model="scope.row.plan_receive_date" @change="chooseDate" :editable='false' type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
								</el-date-picker>
							</template>
						</el-table-column>
						<el-table-column prop="event" label="事件" width="200" sortable>
						</el-table-column>
						<el-table-column prop="distribute_money" label="分配本金（万元）" width="200" sortable>
						</el-table-column>
						<el-table-column prop="distribute_income" label="分配收益-税前（万元）" width="200" sortable>
						</el-table-column>
						<el-table-column prop="sum_assign_fee" label="分配总额（万元）" width="200" sortable>
						</el-table-column>
						<el-table-column prop="unit" label="币种" width="200" sortable>
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--明细模块开始-->
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">分配明细</span>
				</div>
			</div>
			<div class="cash_flow_table_title flex_end">
				<div @click="downloadExcel" class="hover_btn flex_center cursor">
					<span class="iconfont icon-daoru"></span>
					<span>下载投资人分配信息模版</span>
				</div>
				<!--<div @click="uploadExcel" class="blue_btn flex_center cursor">
					<span class="iconfont icon-daoru"></span>
					<span>导入投资人分配信息</span>
				</div>-->
				<div class="">
					<label for="upload-fund-input">
						<div class="table-group-title-btn btn-blue-border">
							<span class="iconfont icon-daochu"></span>导入投资人分配信息
						</div>
					</label>
					<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadManageCompanyInfoExcel($event)" />
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="under_fund_main table">
					<el-table :data="detail_table_data" border style="width: 100%">
						<el-table-column prop="event" label="序号" width="100" sortable>
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column prop="investor_name" label="合伙人名称" width="200" sortable>
						</el-table-column>
						<el-table-column prop="subscribe" label="实缴出资（万元）" width="200" sortable>
						</el-table-column>
						<el-table-column prop="subscribe_scale" label="实缴占比" width="150" sortable>
							<template slot-scope="scope">
								{{ scope.row.subscribe_scale }}&nbsp;%
							</template>
						</el-table-column>
						<el-table-column prop="receive_date" label="实际到账日期" width="200" sortable>
							<template slot-scope="scope">
								<el-date-picker v-model="scope.row.receive_date" :editable='false' type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
								</el-date-picker>
							</template>
						</el-table-column>
						<el-table-column prop="principal_distribution" label="本金分配（万元）" width="200" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.principal_distribution"  v-on:input="calculate(scope.$index) "  placeholder="请输入" clearable>
								</el-input>
								<!-- v-on:input="calculate(scope.$index)" -->
								<!-- @blur="toCheck(scope.$index) -->
							</template>
						</el-table-column>
						<el-table-column prop="income_before_tax" label="分配收益-税前（万元）" width="200" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.income_before_tax" v-on:input="calculate(scope.$index)" placeholder="" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="interest" label="应付利息/利息收入（万元）" width="230" sortable>
							<template slot-scope="scope">
								<el-input v-model="scope.row.interest" placeholder="" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="income_after_interest" label="息后收益分配-税前（万元）" width="230" sortable>
							<template slot-scope="scope">
								<el-input v-model="scope.row.income_after_interest" placeholder="" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="service_fee" label="代扣代缴税款（20%）" width="200" sortable>
							<template slot-scope="scope">
								<el-input v-model="scope.row.service_fee" placeholder="" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="final_income" label="息后税后收益分配（万元）" width="230" sortable>
							<template slot-scope="scope">
								<el-input v-model="scope.row.final_income" v-on:input="calculate(scope.$index)" placeholder="" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="this_sum_distribution" label="本次分配总额（万元）" width="200" sortable>
							
						</el-table-column>
						<el-table-column prop="last_sum_distribution" label="上次累计分配总额（万元）" width="230" sortable>
							<!--<template slot-scope="scope">
								<el-input v-model="scope.row.last_sum_distribution" placeholder="" clearable>
								</el-input>
							</template>-->
						</el-table-column>
						<el-table-column prop="sum_distribution" label="累计分配金额（万元）" width="200" sortable>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		<div v-if="is_add_page" class="submit_btn_box flex_center">
			<button @click="addCancel" class="hover_btn">取消</button>
			<button @click="addSave" class="blue_btn">保存</button>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn,format_money } from '../../config/mUtils'
	import { getDistributeAddInfoMotherCashApi,addDistributeMotherCashApi,downloadDistributeMotherCashApi,uploadDistributeMotherCashApi,getEventByDateApi,getRateApi } from '../../config/api'
	
	export default {
		data() {
			return {
				id: '',
				type: 1,//1代表母基金模块；2代表自管基金模块
				is_add_page: 1,
				rate: 1,// 实时汇率
				
				total_selected_money: 0,
				total_selected: [],
				relate_table_data: [],
				total_table_data: [
					{
						fof_id: '',
						event: '',
						plan_receive_date: '',
						sum_assign_fee: '',//分配总额
						scale: '',//本金占比
						
						unit: '',
						distribute_money: '',
						distribute_income: '',
						event_count: '',
					}
				],
				detail_table_data:[],
			}
		},
		created() {
			this.is_add_page = this.$route.query.is_add_page;
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			this.getAddInfo()
			this.getRate()
		},
		computed: {

		},
		mounted() {},
		methods: {
			getAddInfo(){
				getDistributeAddInfoMotherCashApi({
					fof_id: this.id,
					difference_sign: this.type
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.relate_table_data = res.data.payload.event_log;
						this.detail_table_data = res.data.payload.detail;
						if (res.data.payload.event_log[0]) {
							this.total_table_data[0].unit = res.data.payload.event_log[0].unit;
						} else{
							this.total_table_data[0].unit = 1;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addSave(){
				// 判断分配总额是否小于已选择的分配总额
				if (this.total_selected_money < this.total_table_data[0].sum_assign_fee) {
					dialogConfirm("info", '分配总额应小于待分配总额');
					return
				}
				
				this.total_table_data[0].fof_id = this.id;
				var a = this.total_table_data[0].distribute_money/this.total_table_data[0].sum_assign_fee;
				this.total_table_data[0].scale = this.toDecimal(a);
				var event = JSON.stringify(this.total_table_data);
				var detail = JSON.stringify(this.detail_table_data);
				var relate = this.total_selected;
				for (var i = 0,max = relate.length;i<max;i++) {
					relate[i]['payback_fof_id'] = relate[i].id;
				}
				var event_log = JSON.stringify(relate);
				let canContinue = true;
				this.detail_table_data.forEach(element => {
					if(!element.receive_date && canContinue){
						canContinue = false
					}
				});
				if(!canContinue){
					dialogConfirm("info", '实际到账日期不可为空!');
					return false;
				}
				addDistributeMotherCashApi({
					fof_id: this.id,
					difference_sign: this.type,
					event: event,
					detail: detail,
					event_log: event_log
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.addCancel()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				downloadDistributeMotherCashApi({
					fof_id: this.id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//导入Excel文件
			uploadManageCompanyInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				uploadDistributeMotherCashApi({},file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						var detail_data = res.data.payload.data;
						this.makeData(detail_data)
					} else {
						dialogConfirm('info', '导入失败，请联系开发人员');
					}
					inputDOM.value = '';
				})
			},
			makeData(data){
				for (var i = 0,max = data.length;i<max;i++) {
					for (var j = 0,max2 = this.detail_table_data.length;j<max2;j++) {
						if (data[i].investor_id == this.detail_table_data[j].investor_id) {
							Object.assign(this.detail_table_data[j], data[i]);
							console.log(this.detail_table_data[j]);
						}
					}
				}
			},
			// 小数点取后四位方法
			toDecimal(x) { 
				var f = parseFloat(x); 
				if (isNaN(f)) { 
					return; 
				}
   				f = Math.round(x*10000)/10000; 
   				return f; 
			},
			
			handleSelectionChange(e) {
				console.log(e);
				var total = 0;
				for (var i = 0,max = e.length;i<max;i++) {
					if (e.unit == 2) {
						total = total + Number(e[i].rest_fee) * rate;
					} else{
						total = total + Number(e[i].rest_fee);
					}
				}
				this.total_selected = e;
				total = total.toFixed(6);
				total = format_money(total)
				this.total_selected_money = total;
			},
		
	      	// 计算所有数据的总和
			calculate(index){
				if(parseFloat(this.detail_table_data[index].subscribe)< parseFloat(this.detail_table_data[index].principal_distribution)){
					dialogConfirm("info", '分配本金应小于或等于实缴出资!');
					this.detail_table_data[index].principal_distribution = 0;
				}
				// 计算单条记录的总额数据
				this.detail_table_data[index].this_sum_distribution = Number(this.detail_table_data[index].principal_distribution) + Number(this.detail_table_data[index].final_income)
				this.detail_table_data[index].sum_distribution = Number(this.detail_table_data[index].this_sum_distribution) + Number(this.detail_table_data[index].last_sum_distribution)
				
				// 计算分配汇总的数据
				let data = this.detail_table_data ;
				// JSON.parse(JSON.stringify(this.detail_table_data)) 
				let totalShouldPay1 = 0;//人民币
				let totalHavePay1 = 0;
				let total_sum_assign_fee = 0;//分配总额
				for (let i=0,max=data.length;i<max;i++) {
					if(!data[i].principal_distribution){
						data[i].principal_distribution = 0;
					}
					if(!data[i].income_before_tax){
						data[i].income_before_tax = 0;
					}
					
					if(!data[i].this_sum_distribution){
						data[i].this_sum_distribution = 0;
					}
					totalShouldPay1 = Number(totalShouldPay1) + Number(data[i].principal_distribution);
					totalHavePay1 = Number(totalHavePay1) + Number(data[i].income_before_tax);
					
					total_sum_assign_fee = Number(total_sum_assign_fee) + Number(data[i].this_sum_distribution);
				}
				this.total_table_data[0].distribute_money = totalShouldPay1;
				this.total_table_data[0].distribute_income = totalHavePay1;
				this.total_table_data[0].sum_assign_fee = total_sum_assign_fee;
			},
			addCancel(){
				if (this.type == 1) {
					var name = 'motherFunRunDataCashFlowHome';
				} else{
					var name = 'selfAdministerFunRunDataCashFlowHome';
				}
				this.$router.push({
					name: name,
					query: {
						id: this.id
					}
				})
			},
			chooseDate(date){
				if (!date) {
					this.total_table_data[0].event = '';
					this.total_table_data[0].event_count = '';
					return
				}
				this.getEventCountData()
			},
			getEventCountData(){
				if (this.type == 1) {
					var type = 2;
				} else {
					var type = 3;
				}
				var date = this.total_table_data[0].plan_receive_date;
				getEventByDateApi({
					type: type,
					id: this.id,
					cash_type: 4,
					date: date,
					fund_type: '',
					fund_id: '',
					event_id: '',
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.total_table_data[0].event = res.data.payload.event_name;
						this.total_table_data[0]['event_count'] = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getRate(){
				getRateApi({
					
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.rate = res.data.payload.exchangeRate
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
