		padding: 0 10px 30px 10px;
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right .delete {
		margin-right: 60px;
		color: #6292F4;
		font-size: 14px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	.cash_flow_table_title .blue_btn{
		margin-left: 20px;
	}
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
	
	/*新增样式*/
	.relete_back_title{
		margin-bottom: 28px;
		font-size: 14px;
		color: #6F7071;
	}
	.relete_back_title span{
		margin-left: 20px;
		font-size: 16px;
		color: #33302D;
	}
	.relete_back_title .blue_btn{
		width: 100px;
		text-align: center;
	}
	.submit_btn_box{
		margin: 35px 0 20px 0;
	}
	.submit_btn_box button{
		height: 36px;
		margin: 0 10px;
		line-height: 36px;
		padding: 0 25px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
</style>


